<template>
    <div class="index">
        <div class="header">
            <div :class="{ active: isbook }" @click="topath('book')">阅读</div>
            <div :class="{ active: isanime }" @click="topath('anime')">
                动漫
            </div>
            <div :class="{ active: ismovie }" @click="topath('movie')">
                电影
            </div>
            <div :class="{ active: isgame }" @click="topath('game')">游戏</div>
        </div>
        <div class="content">
            <div class="content_book" v-show="isbook">
                <div
                    class="cont_wrapper"
                    v-for="item in book"
                    :key="item.key"
                    v-show="item.imgSrc"
                >
                    <img class="vertical" :src="item.imgSrc" alt="" />
                    <div class="cont-title">{{ item.title }}</div>
                </div>
            </div>
            <div class="content_anime" v-show="isanime">
                <div class="c_a_top" v-show="isanitop">
                    <div
                        class="cont_wrapper"
                        v-for="item in anime"
                        :key="item.key"
                        v-show="item.imgSrc"
                    >
                        <img
                            class="vertical"
                            :src="item.imgSrc"
                            @click="showanimg(item.title)"
                            alt=""
                        />
                        <div class="cont-title">
                            {{ item.title }}
                        </div>
                    </div>
                </div>
                <div class="c_a_bottom" v-show="!isanitop">
                    <div
                        class="cont-img"
                        v-for="img in imgnum"
                        :key="img"
                        v-show="isanimg2"
                    >
                        <img
                            :src="
                                'https://gitee.com/ginkgoleaves/images/raw/master/img/' +
                                animg2title +
                                '/' +
                                img +
                                '.jpg'
                            "
                            onerror="this.src=''"
                            class="c-img"
                        />
                    </div>
                </div>
                <div class="ani_btn" v-show="!isanitop" @click="returnimg">
                    返回
                </div>
                <div class="ani_btn2" v-show="!isanitop" @click="showmore">
                    显示更多
                </div>
            </div>
            <div class="content_movie" v-show="ismovie">
                <!-- <div
                class="cont_wrapper"
                v-for="item in movie"
                :key="item.key"
                v-show="item.imgSrc"
            >
                <img class="vertical" :src="item.imgSrc" alt="" />
                <div class="cont-title">{{ item.title }}</div>
            </div> -->
                <table border="0" cellspacing="0">
                    <tr>
                        <td>序号</td>
                        <td>名字</td>
                        <td>
                            分数
                            <span
                                style="cursor: pointer"
                                @click="sortorder('score')"
                            >
                                x
                            </span>
                        </td>
                        <td>
                            时间
                            <span
                                style="cursor: pointer"
                                @click="sortorder('date')"
                            >
                                x
                            </span>
                        </td>
                        <td>图片</td>
                    </tr>
                    <tr
                        v-for="(item, key) in movie"
                        :key="item.title"
                        v-show="item.imgSrc"
                    >
                        <td>{{ key + 1 }}</td>
                        <td>{{ item.title }}</td>
                        <td>{{ item.score }}</td>
                        <td style="text-align: left">{{ item.date2 }}</td>
                        <td style="padding: 5px 15px">
                            <img
                                style="width: 100px; height: 140px"
                                class="vertical"
                                :src="item.imgSrc"
                                alt=""
                            />
                        </td>
                    </tr>
                </table>
            </div>
            <div class="content_game" v-show="isgame">
                <button @click="isg1 = !isg1">切换</button>
                <div v-show="isg1">
                    <h3>FC</h3>
                    <div class="cont_wrapper_type">
                        <div
                            v-for="item in gamesort[0]"
                            :key="item.key"
                            v-show="item.imgSrc"
                        >
                            <img class="vertical" :src="item.imgSrc" alt="" />
                            <div class="cont-title">{{ item.title }}</div>
                            <div class="cont-title">{{ item.type }}</div>
                        </div>
                    </div>
                    <h3>GBA</h3>
                    <div class="cont_wrapper_type">
                        <div
                            v-for="item in gamesort[1]"
                            :key="item.key"
                            v-show="item.imgSrc"
                        >
                            <img class="vertical" :src="item.imgSrc" alt="" />
                            <div class="cont-title">{{ item.title }}</div>
                            <div class="cont-title">{{ item.type }}</div>
                        </div>
                    </div>
                    <h3>PSP</h3>
                    <div class="cont_wrapper_type">
                        <div
                            v-for="item in gamesort[2]"
                            :key="item.key"
                            v-show="item.imgSrc"
                        >
                            <img class="vertical" :src="item.imgSrc" alt="" />
                            <div class="cont-title">{{ item.title }}</div>
                            <div class="cont-title">{{ item.type }}</div>
                        </div>
                    </div>
                    <h3>页游</h3>
                    <div class="cont_wrapper_type">
                        <div
                            v-for="item in gamesort[3]"
                            :key="item.key"
                            v-show="item.imgSrc"
                        >
                            <img class="vertical" :src="item.imgSrc" alt="" />
                            <div class="cont-title">{{ item.title }}</div>
                            <div class="cont-title">{{ item.type }}</div>
                        </div>
                    </div>
                    <h3>端游</h3>
                    <div class="cont_wrapper_type">
                        <div
                            v-for="item in gamesort[4]"
                            :key="item.key"
                            v-show="item.imgSrc"
                        >
                            <img class="vertical" :src="item.imgSrc" alt="" />
                            <div class="cont-title">{{ item.title }}</div>
                            <div class="cont-title">{{ item.type }}</div>
                        </div>
                    </div>
                </div>
                <div v-show="!isg1">
                    <div
                        class="cont_wrapper"
                        v-for="item in gameall"
                        :key="item.key"
                        v-show="item.imgSrc"
                    >
                        <img class="vertical" :src="item.imgSrc" alt="" />
                        <div class="cont-title">{{ item.title }}</div>
                        <div class="cont-title">{{ item.date }}</div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { book } from "./data.json";
import { anime } from "./data.json";
import { movie } from "./data.json";
import { game } from "./data.json";
export default {
    name: "life",
    data() {
        return {
            book: [],
            anime: [],
            movie: [],
            gamesort: [],
            gameall: [],
            isbook: false,
            isanime: false,
            ismovie: false,
            isgame: false,
            isg1: false,
            isanimg2: false,
            animg2title: "",
            imgnum: 0,
            isanitop: true,
        };
    },
    methods: {
        topath(path) {
            switch (path) {
                case "book":
                    this.book = book;
                    this.isbook = true;
                    this.isanime = this.ismovie = this.isgame = false;
                    break;
                case "anime":
                    this.anime = anime;
                    this.isanime = true;
                    this.isbook = this.ismovie = this.isgame = false;
                    // 返回
                    this.isanitop = true;
                    this.imgnum = 0;
                    break;
                case "movie":
                    this.movie = movie;
                    this.ismovie = true;
                    this.isbook = this.isanime = this.isgame = false;
                    break;
                case "game":
                    this.gameall = game;
                    this.isgame = true;
                    this.isbook = this.isanime = this.ismovie = false;
                    this.gamesort = [[], [], [], [], []];
                    game.forEach((item) => {
                        if (item.type == "host_fc") {
                            this.gamesort[0].push({
                                imgSrc: item.imgSrc,
                                title: item.title,
                            });
                        } else if (item.type == "host_gba") {
                            this.gamesort[1].push({
                                imgSrc: item.imgSrc,
                                title: item.title,
                            });
                        } else if (item.type == "host_psp") {
                            this.gamesort[2].push({
                                imgSrc: item.imgSrc,
                                title: item.title,
                            });
                        } else if (item.type == "pc_web") {
                            this.gamesort[3].push({
                                imgSrc: item.imgSrc,
                                title: item.title,
                            });
                        } else if (item.type == "pc_end") {
                            this.gamesort[4].push({
                                imgSrc: item.imgSrc,
                                title: item.title,
                            });
                        }
                    });
                    break;
            }
        },
        // movie排序
        sortorder(type) {
            this.movie = [];
            let len = movie.length;

            if (type == "score") {
                for (let i = 0; i < len - 1; i++) {
                    for (let j = 0; j < len - 1 - i; j++) {
                        if (movie[j].score < movie[j + 1].score) {
                            let temp = movie[j + 1];
                            movie[j + 1] = movie[j];
                            movie[j] = temp;
                        }
                    }
                }
            } else if (type == "date") {
                for (let i = 0; i < len - 1; i++) {
                    for (let j = 0; j < len - 1 - i; j++) {
                        if (movie[j].date < movie[j + 1].date) {
                            let temp = movie[j + 1];
                            movie[j + 1] = movie[j];
                            movie[j] = temp;
                        }
                    }
                }
            }

            this.movie = movie;
        },
        // ani图片
        showanimg(title) {
            this.isanitop = false;
            this.imgnum = 10;
            this.isanimg2 = false;
            this.anime.forEach((item) => {
                if (title == item.title) {
                    this.animg2title = item.src2;
                    setTimeout(() => {
                        this.isanimg2 = true;
                    }, 300);
                }
            });
        },
        returnimg() {
            this.isanitop = true;
            this.imgnum = 0;
        },
        showmore() {
            this.imgnum = 20;
        },
    },
    mounted() {
        // movie根据date获取date2
        let len = movie.length;
        for (let i = 0; i < len; i++) {
            let year = Math.floor(movie[i].date / 10000);
            let month = Math.floor((movie[i].date / 100) % 100);
            let day = movie[i].date % 100;
            movie[i].date2 = `${year}年${month}月${day}日`;
        }
    },
};
</script>
    
<style lang="less" scoped>
* {
    padding: 0;
    margin: 0;
}
.index {
    .header {
        max-width: 300px;
        margin: 50px auto 0;
        display: flex;
        justify-content: space-around;
        div {
            cursor: pointer;
        }
        div:hover {
            color: orange;
        }
        .active {
            color: orange;
        }
    }
    .content {
        .content_book {
            display: flex;
            // justify-content: space-around;
            flex-wrap: wrap;
            .cont_wrapper {
                padding: 5px 10px 10px;
                .vertical {
                    width: 160px;
                    height: 200px;
                }
                img {
                    width: 100%;
                    height: 100%;
                    display: block;
                }
                .cont-title {
                    color: pink;
                    text-align: center;
                    cursor: pointer;
                }
            }
        }
        .content_anime {
            position: relative;
            .c_a_top {
                display: flex;
                // justify-content: space-around;
                flex-wrap: wrap;
                .cont_wrapper {
                    padding: 5px 10px 10px;
                    .vertical {
                        width: 160px;
                        height: 200px;
                        cursor: pointer;
                    }
                    img {
                        width: 100%;
                        height: 100%;
                        display: block;
                    }
                    .cont-title {
                        color: pink;
                        text-align: center;
                        cursor: pointer;
                    }
                }
            }
            .c_a_bottom {
                display: flex;
                flex-wrap: wrap;
                .cont-img {
                    .c-img {
                        width: 160px;
                        height: 200px;
                    }
                    img {
                        width: 100%;
                        height: 100% !important;
                        display: block;
                    }
                    img:not([src]),
                    img[src=""] {
                        display: none;
                    }
                    .no {
                        text-align: center;
                    }
                }
            }
            .ani_btn {
                position: fixed;
                right: 90px;
                // bottom: 50%;
                cursor: pointer;
            }
            .ani_btn2 {
                position: fixed;
                right: 20px;
                // bottom: 50%;
                cursor: pointer;
            }
        }
        .content_movie {
            table {
                border-collapse: collapse;
                tr {
                    td {
                        border: 1px solid pink;
                        text-align: center;
                    }
                }
            }
        }
        .content_game {
            button {
                position: absolute;
                right: 20%;
                border: 1px solid rgb(121, 146, 236);
                background: #ffffff;
                padding: 3px 5px;
                cursor: pointer;
            }
            button:hover {
                border: 1px solid pink;
                color: pink;
            }
            .cont_wrapper_type {
                padding: 5px 10px 10px;
                display: flex;
                // justify-content: space-around;
                flex-wrap: wrap;
                div {
                    .vertical {
                        width: 160px;
                        height: 200px;
                    }
                    img {
                        width: 100%;
                        height: 100%;
                        display: block;
                    }
                    .cont-title {
                        color: pink;
                        text-align: center;
                        cursor: pointer;
                    }
                }
            }
            .cont_wrapper {
                padding: 5px 10px 10px;
                width: 160px;
                .vertical {
                    width: 160px;
                    height: 200px;
                }
                img {
                    width: 100%;
                    height: 100%;
                }
                .cont-title {
                    width: 100%;
                    color: pink;
                    text-align: center;
                    cursor: pointer;
                }
            }
        }
    }
}
</style>